import React, { memo } from 'react';
import { Collapse } from 'antd';
import { CaretRightOutlined } from '@ant-design/icons';


import { CollapseWrapper } from "./style"
import { NavLink } from "react-router-dom"
const { Panel } = Collapse;

const WItemCollapse = memo((props) => {

  const data = props.data

  return (
    <CollapseWrapper>
      <Collapse
        bordered={false}
        expandIcon={({ isActive }) => <CaretRightOutlined rotate={isActive ? 90 : 0} />}
        className="site-collapse-custom-collapse"
      >
        {
          // console.log(data)
          data.map((item, index) => {
            return (
              <Panel header={item.header} key={index} className="site-collapse-custom-panel">
              {
                item.items.map((linkItem, indey) => {
                  return (
                    <NavLink className="item" key={indey} to={linkItem.link}  >{linkItem.title}</NavLink>
                  )
                })
              }
            </Panel>
            )
          })
        }
      </Collapse>
    </CollapseWrapper>
    
  );
});

export default WItemCollapse;