import React, { memo } from 'react';

import app from "@/weapps"

const Vibrate = memo(() => {
  return (
    <div>
      <span style={{
        display: "block",
        margin: "40px auto",
        fontSize: "16px",
        textAlign: "center"
      }}>Vibrate/Long/Short</span>
      <button className="button" onClick={() => {
        app.vibrateLong({
          success: () => {
            console.log("vibrateLong success")
          },
          fail: () => {
            console.log("vibrateLong fail")
          }
        })
      }}></button>
      <button className="button" onClick={() => {
        app.vibrateShort({
          type:"heavy",
          success: () => {
            console.log("vibrateShort success")
          },
          fail: () => {
            console.log("vibrateShort fail")
          }
        })
      }}></button>
    </div>
  );
});

export default Vibrate;