import { 
  useEffect, 
  useCallback
} from 'react';

import app from "@/weapps"

export const useListenner = (onApi, offApi, callback, dependencies = []) => {
  // eslint-disable-next-line
  const cb = useCallback(callback, dependencies)
  useEffect(_ => {
    app[onApi](cb)
    return () => {
      app[offApi](cb)
    }
  }, [onApi, offApi, cb])
}