import React, { 
  memo,
  useState
} from 'react';

import  { PhoneWapper } from "./style"

import app from "@/weapps"

const WPhone = memo(() => {

  const [number, setNumber] = useState("")

  return (
    <PhoneWapper>
      <span className="title">makePhoneCall</span>
      <div className="content">
        <span></span>
        <input type="text" value={number} onChange={(e) => {
          setNumber(e.target.value)
        }}/>
        {/* <div className="line"></div> */}
      </div>
      <button className="button" disabled={number.length === 0 } onClick={() => {
        app.makePhoneCall({
          phoneNumber: number,
          success: () => {
            console.log("makePhoneCall success")
          },
          fail: () => {
            console.log("makePhoneCall success")
          }
        })
      }}></button>
      
    </PhoneWapper>
  );
});

export default WPhone