import React, { 
  memo,
  useState
} from 'react';

import  { IbeaconWapper } from "./style"

import app from "@/weapps"
import { useListenner } from "@/common/hook.js"

const WIbeacon = memo(() => {

  const [UUID, setUUID] = useState("")
  const [iBeacons, setIBeacons] = useState([])
  const [isSearching, setIsSearching] = useState(false)

  useListenner("onBeaconUpdate", "offBeaconUpdate", (res) => {
    setIBeacons(res.beacons)
  })

  return (
    <IbeaconWapper>
      <span className="title">getNetworkType</span>
      <span className="info">iBeaconUUID</span>
      <input type="text" id="" value={UUID} onChange={(e) => {
        setUUID(e.target.value)
      }}/>
      <button className="button" disabled={isSearching} onClick={() => {
        if (UUID.length === 0) {
          app.showToast({
            title: "UUID"
          })
          return
        }
        app.startBeaconDiscovery({
          uuids:[UUID],
          ignoreBluetoothAvailable: true,
          success: () => {
            console.log("startBeaconDiscovery success")
            setIsSearching(true)
          },
          fail : (err) => {
            console.log("fail to startBeaconDiscovery:", err)
          }
        })
      }}>iBeacon</button>
      <button className="button" disabled={!isSearching} onClick={() => {
        app.stopBeaconDiscovery({
          success: () => {
            console.log("stopBeaconDiscovery success")
            setIsSearching(false)
          },
          fail : (err) => {
            console.log("fail to stopBeaconDiscovery:", err)
          }
        })
      }}></button>
      <div className="content">
        <span>{iBeacons.length}:</span>
        <div className="scroll">
          { 
          iBeacons.map((item) => {
            return (
              <div className="item">
                <span>{`UUID:${item.uuid}`}</span>
                <span>{`major:${item.major}`}</span>
                <span>{`minor:${item.minor}`}</span>
                <span>{`proximity:${item.proximity}`}</span>
                <span>{`accuracy:${item.accuracy}`}</span>
                <span>{`rssi:${item.rssi}`}</span>
              </div>
            )
          }) 
          }
        </div>
      </div>
    </IbeaconWapper>
  );
});

export default WIbeacon;