import React, { 
  memo, 
  useState
} from 'react';
import  {AccelerometerWrapper} from "./style"
import app from "@/weapps"
import { useListenner } from "@/common/hook.js"

const WAccelerometer = memo(() => {
  const [res, setRes] = useState(null)
  const [isMonitoring, setInMonitoring] = useState(false)

  useListenner("onAccelerometerChange", "offAccelerometerChange", (res) => {
    setRes(res)
  })

  return (
    <AccelerometerWrapper>
      <span className="title">onAccelerometerChange</span>
      <div className="content">
        <span className="accelerometer-title"></span>
        { res && res.x && 
          <div className="item">
            <span className="name">X</span>
            <span className="info">{res.x}</span>
          </div>
        }
        { res && res.y && 
          <div className="item">
            <span className="name">Y</span>
            <span className="info">{res.y}</span>
          </div>
        }
        { res && res.z && 
          <div className="item">
            <span className="name">Z</span>
            <span className="info">{res.z}</span>
          </div>
        }
      </div>
      <button className="button" disabled={isMonitoring} onClick={()=> {
        app.startAccelerometer({
          success:() => {
            setInMonitoring(true)
            console.log("startAccelerometer success")
          },
          fail:()=>{
            console.log("startAccelerometer fail")
          }
        })
      }}></button>

      <button className="button" disabled={!isMonitoring} onClick={
        ()=> {
          app.stopAccelerometer({
            success:() => {
              setInMonitoring(false)
              console.log("stopAccelerometer success")
            },
            fail:()=>{
              console.log("stopAccelerometer fail")
            }
          })
        }
      }></button>
    </AccelerometerWrapper>
  );
});

export default WAccelerometer;