import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  "setClipboardData",
  "getClipboardData"
]

const ClipBoard = {}

for (const api of apis) {
  ClipBoard[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default ClipBoard