export const arrayBufferToBase64 = (buffer) => {
  let binary = '';
  const bytes = new Uint8Array(buffer);
  const len = bytes.byteLength
  for (let i = 0; i < len; i++) {
      binary += String.fromCharCode(bytes[i]);
  }
  return window.btoa(binary);
}

export const base64ToArrayBuffer = (base64) => {
  const string = window.atob(base64);
  const len = string.length;
  const bytes = new Uint8Array(len);
  for (let i = 0; i < len; i++) {
      bytes[i] = string.charCodeAt(i);
  }
  return bytes.buffer;
}

export const changeArrayBufferPropToBase64 = (object) => {

  for (const key in object) {
    if (object.hasOwnProperty(key)) {
      const element = object[key];
      if (element && element instanceof ArrayBuffer) {
        object[key] = arrayBufferToBase64(element)
      } else if (element && typeof element === "object" && !Array.isArray(element)) {
        object[key] = changeArrayBufferPropToBase64(element)
      } else if (Array.isArray(element)) {
        const array = []
        for (const item of element) {
          array.push(changeArrayBufferPropToBase64(item))
        }
        object[key] = array
      }
    }
  }
  return object
}

export const utf8StringToArrayBuffer = (str) => {
	let bytes = []
	let len,c
	len = str.length
	for(let i = 0; i < len; i++){
		c = str.charCodeAt(i)
		if(c >= 0x010000 && c <= 0x10FFFF){
			bytes.push(((c >> 18) & 0x07) | 0xF0)
			bytes.push(((c >> 12) & 0x3F) | 0x80)
			bytes.push(((c >> 6) & 0x3F) | 0x80)
			bytes.push((c & 0x3F) | 0x80)
		}else if(c >= 0x000800 && c <= 0x00FFFF){
			bytes.push(((c >> 12) & 0x0F) | 0xE0)
			bytes.push(((c >> 6) & 0x3F) | 0x80)
			bytes.push((c & 0x3F) | 0x80)
		}else if(c >= 0x000080 && c <= 0x0007FF){
			bytes.push(((c >> 6) & 0x1F) | 0xC0)
			bytes.push((c & 0x3F) | 0x80)
		}else{
			bytes.push(c & 0xFF)
		}
  }
  const array = new Int8Array(bytes.length);
  for(let i in bytes){
    array[i] =bytes[i]
  }
	return array.buffer
}

export const arrayBufferToUtf8String = (arr) => {
  if(typeof arr === 'string') {  
      return arr
  }  
  let dataview = new DataView(arr.data)
  let ints = new Uint8Array(arr.data.byteLength)
  for(let i = 0; i < ints.length; i++){
    ints[i] = dataview.getUint8(i)
  }
  arr = ints
  let str = '', 
      _arr = arr  
  for(let i = 0; i < _arr.length; i++) {  
      let one = _arr[i].toString(2),
          v = one.match(/^1+?(?=0)/) 
      if(v && one.length === 8) {  
          let bytesLength = v[0].length
          let store = _arr[i].toString(2).slice(7 - bytesLength)
          for(let st = 1; st < bytesLength; st++) {
              store += _arr[st + i].toString(2).slice(2)
          }
          str += String.fromCharCode(parseInt(store, 2))
          i += bytesLength - 1
      } else {  
          str += String.fromCharCode(_arr[i])
      }  
  }  
  return str
}