import React, { 
  memo, useCallback, useState
} from 'react';
import  { RequestWrapper } from "./style"
import app from "@/weapps"


const WRequest = memo(() => {
  
  const [requestTask, setRequestTask] = useState(null)

  const onHeadersReceived = useCallback((res) => {
    console.log(`header: ${res.header}`)
    console.log(`header-description: ${JSON.stringify(res.header)}`)
  }, [])


  return (
    <RequestWrapper>
      <span className="title">request</span>
      <div className="content">
        <span></span>
      </div>
      <button className="button" onClick={() => {
        if (requestTask) {
          requestTask.offHeadersReceived(onHeadersReceived)
        }
        const task = app.request({
          url: "https://mp.weixin.qq.com",
          data: {
            noncestr: Date.now()
          },
          success: () => {
            app.showToast({
              title: '',
              icon: 'success',
              mask: true,
              duration: 2000,
            })
          },
          fail: (res) =>{
            console.log("request fail:", res)
          }
        })
        if (task) {
          task.onHeadersReceived(onHeadersReceived)
        }
        setRequestTask(task)
      }}>request</button>
    </RequestWrapper>
  );
});

export default WRequest;