import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "stopCompass",
  "startCompass"
]

const onCallbacks = [
  "onCompassChange"
]

const offCallbacks = [
  "offCompassChange"
]

const Compass = {}

for (const api of apis) {
  Compass[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Compass[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  Compass[callback] = (cb) => {
    offCallback(callback, cb)
  }
}
export default Compass