import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  "showTabBarRedDot",
  "showTabBar",
  "setTabBarStyle",
  "setTabBarItem",
  "setTabBarBadge",
  "removeTabBarBadge",
  "hideTabBarRedDot",
  "hideTabBar"
]

const TabBar = {}

for (const api of apis) {
  TabBar[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default TabBar