import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  "vibrateShort",
  "vibrateLong"
]

const Vibrate = {}

for (const api of apis) {
  Vibrate[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default Vibrate