import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  "showToast",
  "showModal",
  "showLoading",
  "showActionSheet",
  "hideToast",
  "hideLoading"
]

const Interaction = {}

for (const api of apis) {
  Interaction[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default Interaction
