import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"

import { changeArrayBufferPropToBase64 } from "../utils"
import Foundation from "../foundation"

class BLEPeripheralServer {
  constructor(serverId) {
    this.serverId = serverId
  }
}

const subApis = [
  "close",
  "addService",
  "removeService",
  "stopAdvertising",
  "writeCharacteristicValue",
  "startAdvertising"
]

const subOnCallbacks = [
  "onCharacteristicReadRequest",
  "onCharacteristicSubscribed",
  "onCharacteristicUnsubscribed"
]

const subOffCallbacks = [
  "offCharacteristicReadRequest",
  "offCharacteristicSubscribed",
  "offCharacteristicUnsubscribed",
  "offCharacteristicWriteRequest"
]

for (const api of subApis) {
  BLEPeripheralServer.prototype[api] = function(object) {
    if (typeof object !== "object")
    object = {}
    const callbackName = createOnceCallback(object)
    object = changeArrayBufferPropToBase64(object)
    object.serverId = this.serverId
    object.operationType = api
    callNativeApi("operateBLEPeripheralServer", washObject(object), callbackName)
  }
}

for (const callback of subOnCallbacks) {
  BLEPeripheralServer.prototype[callback] = function(cb) {
    const data = {
      serverId: this.serverId,
      operationType: callback
    }
    onCallback("operateBLEPeripheralServer", cb, data, callback)
  }
}

BLEPeripheralServer.prototype.onCharacteristicWriteRequest = function(cb) {
  const data = {
    serverId: this.serverId,
    operationType: "onCharacteristicWriteRequest"
  }
  cb.wrapper = (res) => {
    if (res.value && typeof res.value === "string") {
      res.value = Foundation.base64ToArrayBuffer(res.value)
    }
    return res
  }
  onCallback("operateBLEPeripheralServer", cb, data, "onCharacteristicWriteRequest")
}


for (const callback of subOffCallbacks) {
  BLEPeripheralServer.prototype[callback] = function(cb)  {
    const data = {
      serverId: this.serverId,
    }
    offCallback(callback, cb, data)
  }
}


//######################################

const onCallbacks = [
  "onBLEPeripheralConnectionStateChanged"
]

const offCallbacks = [
  "offBLEPeripheralConnectionStateChanged"
]

const BLEPeripheral = {}


BLEPeripheral.createBLEPeripheralServer = (object = {}) => {
  if (typeof object.success == "function") {
    object.success.wrapper = (res) => {
      const serverId = res.server
      const server = new BLEPeripheralServer(serverId)
      return {
        server
      }
    }
  }
  const callbackName = createOnceCallback(object)
  callNativeApi("createBLEPeripheralServer", washObject(object), callbackName)
}

for (const callback of onCallbacks) {
  BLEPeripheral[callback] = (cb) => {
    onCallback(callback, cb)
  }
}

for (const callback of offCallbacks) {
  BLEPeripheral[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default BLEPeripheral
