import React, { memo, useState } from 'react';
import  { BatteryWapper } from "./style"
import app from "@/weapps"

const WBattery = memo(() => {

  const [info, setInfo] = useState({})

  return (
    <BatteryWapper>
      <span className="title">geBatteryInfo</span>
      <div className="content-wrapper">
        <div className="item">
          <span className="title">
            
          </span>
          
          <span className="info">
            {info.level || ""}
          </span>
        </div>
        <div className="content-line"></div>
        <div className="item">
          <span className="title">
            
          </span>
          <span className="info">
            { (info.isCharging && "") ||
              (info.isCharging === false && "") ||
              ""
            }
          </span>
        </div>
      </div>
      <button className="button" onClick={
        ()=> {
          app.getBatteryInfo({
            success:(res) => {
              setInfo(res)
            },
            fail: () => {
              console.log("getBatteryInfo fail")
            }
          })
        }
      }></button>
    </BatteryWapper>
  );
});

export default WBattery;