import React, { memo, useState } from 'react';
import  { ClipboardWapper } from "./style"
import app from "@/weapps"

const WClipboard = memo(() => {

  const [sourceData, setSourceData] = useState("edit and copy me")
  const [pasteData, setPasteData] = useState("")

  return (
    <ClipboardWapper>
      <span className="title">get/set/clipboardData</span>
      <div className="content-wrapper">
        <div className="item">
          <span className="title">
            Copy
          </span>
          <input  className="input" type="text" value={sourceData} onChange={
            (e) => {
              setSourceData(e.target.value)
            }
          } />
        </div>
        <div className="content-line"></div>
        <div className="item">
          <span className="title">
            Paste
          </span>
          <span className="info">
            {pasteData}
          </span>
        </div>
      </div>
      <button className="button" onClick={
        ()=> {
          app.setClipboardData({
            data: sourceData,
            success: () => {
              console.log("setClipboardData success")
            }
          })
        }
      }></button>
      <button className="button" onClick={() => {
        app.getClipboardData({
          success: (res) => {
            setPasteData(res.data)
          }
        })
      }}></button>
    </ClipboardWapper>
  );
});

export default WClipboard;