import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"

import Foundation from "../foundation"
import { changeArrayBufferPropToBase64 } from "../utils"


const apis = [
  "setBLEMTU",
  "readBLECharacteristicValue",
  "notifyBLECharacteristicValueChange",
  "getBLEDeviceServices",
  "getBLEDeviceRSSI",
  "getBLEDeviceCharacteristics",
  "createBLEConnection",
  "closeBLEConnection"
]

const onCallbacks = [
  "onBLEConnectionStateChange",
  // "onBLECharacteristicValueChange"
]

const offCallbacks = [
  "offBLEConnectionStateChange",
  "offBLECharacteristicValueChange"
]

const BluetoochLowEnergy = {}

for (const api of apis) {
  BluetoochLowEnergy[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

//
BluetoochLowEnergy.writeBLECharacteristicValue = (object) => {
  const callbackName = createOnceCallback(object)
  object = washObject(object)
  object = changeArrayBufferPropToBase64(object)
  console.log("object:", object)
  callNativeApi("writeBLECharacteristicValue", object, callbackName)
}

//
BluetoochLowEnergy.makeBluetoothPair = (object) => {
  const callbackName = createOnceCallback(object)
  object = washObject(object)
  object = changeArrayBufferPropToBase64(object)
  callNativeApi("makeBluetoothPair", object, callbackName)
}

for (const callback of onCallbacks) {
  BluetoochLowEnergy[callback] = (cb) => {
    onCallback(callback, cb)
  }
}

//
BluetoochLowEnergy.onBLECharacteristicValueChange = (cb) => {
  cb.wrapper = (res) => {
    let value = res.value;
    if (value && typeof value === "string") {
      value = Foundation.base64ToArrayBuffer(value)
    }
    return {
      ...res,
      value
    }
  }
  onCallback("onBLECharacteristicValueChange", cb)
}

for (const callback of offCallbacks) {
  BluetoochLowEnergy[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default BluetoochLowEnergy