import React, { 
  memo, 
  useState
} from 'react';
import  { PeripheralServerWrapper } from "./style"
import app from "@/weapps"

const uuid3 = '0C76801A-62EB-45E5-96A8-37C8882ABB2B'
const serviceId = 'D0611E78-BBB4-4591-A5F8-487910AE4301'
const characteristicId = '8667556C-9A37-4C91-84ED-54EE27D90049'

const WPeripheralServer = memo(() => {

  const [server, setServer] = useState({})

  const [valueToWrite, setValueToWrite] = useState("")

  const createBLEPeripheralServer = () => {
    app.createBLEPeripheralServer({
      success: (res) => {
        console.log('createBLEPeripheralServer', res)
        setServer(res.server)
        app.showToast({
          title: ' server ',
        })
        res.server.onCharacteristicReadRequest(result => {
          const { serviceId, characteristicId, callbackId } = result
          const buffer = new ArrayBuffer(1)
          const dataView = new DataView(buffer)
          const newValue = Math.ceil(Math.random() * 10)
          dataView.setUint8(0, newValue)
          console.log('onCharacteristicReadRequest', result, newValue)

          res.server.writeCharacteristicValue({
            serviceId,
            characteristicId,
            value: buffer,
            needNotify: true,
            callbackId
          })
        })

        res.server.onCharacteristicWriteRequest(result => {
          console.log('onCharacteristicWriteRequest', result)
          const { serviceId, characteristicId, value, callbackId } = result
          console.log("callbackId:", callbackId)
          app.showToast({
            title: ''
          })

          res.server.writeCharacteristicValue({
            serviceId,
            characteristicId,
            value,
            needNotify: true,
            callbackId
          })
        })
      }
    })
  }

  const createServer = () => {
    app.openBluetoothAdapter({
      mode: 'peripheral',
      success: (res) => {
        console.log('openBluetoothAdapter success', res)
        createBLEPeripheralServer()
      },
      fail: (res) => {
        console.log(res);
        app.showToast({
          title: ` : ${res.errCode}`,
          icon:'none'
        })
        if (res.errCode === 10001) {
          app.onBluetoothAdapterStateChange(function (res) {
            console.log('onBluetoothAdapterStateChange', res)
            if (res.available) {
              createBLEPeripheralServer()
            }
          })
        }
      }
    })
  }

  const addService = () => {
    const buffer = new ArrayBuffer(1)
    const dataView = new DataView(buffer)
    dataView.setUint8(0, 2)
    const descriptorBuffer = new ArrayBuffer(1)
    const dataView2 = new DataView(descriptorBuffer)
    dataView2.setInt8(0, 3)
    const service = {
      uuid: serviceId,
      characteristics: [{
        uuid: characteristicId,
        properties: {
          write: true,
          read: true,
          notify: true,
          indicate: true
        },
        permission: {
          readable: true,
          writeable: true,
          readEncryptionRequired: true,
          writeEncryptionRequired: true
        },
        // value: buffer,
        // descriptors: [{
        //   uuid: uuid3,
        //   permission: {
        //     write: true,
        //     read: true
        //   },
        //   value: descriptorBuffer
        // }]
      }]
    }

    server.addService({
      service,
      success: (res) => {
        console.log('add Service', res)
        app.showToast({
          title: '',
        })
      },
      fail: (rej) => {
        console.log(rej);
      if (rej.errCode === 10001) {
        app.showToast({
          title: '',
        })
      } else {
        app.showModal({
          title: '',
          content: `: ${rej.errCode}`
        })
      }
      }
    })
  }

  const removeService =() => {
    server.removeService({
      serviceId: serviceId,
      success: (res) => {
        app.showToast({
          title: '',
        })
        console.log('removeService', res)
      }
    })
  }

  const closeServer = () => {
    server.close({
      success: () => {
        app.showToast({
          title: ' server',
        })
        setServer({})
      }
    })
  }

  const closeBluetoothAdapter = () => {
    app.showToast({
      title: '',
    })
    app.closeBluetoothAdapter()
  }

  const startAdvertising = () => {
    const buffer = new ArrayBuffer(1)
    const dataView = new DataView(buffer)
    dataView.setInt8(0, 4)
    console.log("")
    server.startAdvertising({
      advertiseRequest: {
        connectable: true,
        deviceName: 'Weapps',
        serviceUuids: [serviceId],
        manufacturerData: [{
          manufacturerId: 'Weapps-pc0',
          manufacturerSpecificData: buffer
        }]
      },
      powerLevel: 'medium',
      success: (res) => {
        console.log('startAdvertising', res)
        app.showToast({
          title: '',
        })
      },
      fail: () => {
        console.log("startAdvertising fail")
      }
    })
  }

  const stopAdvertising = () => {
    server.stopAdvertising()
    app.showToast({
      title: '',
    })
  }

  const writeData = () => {
    console.log('writeData')
    const n = valueToWrite * 1
    const buffer = new ArrayBuffer(1)
    const dataView = new DataView(buffer)
    dataView.setUint8(0, n)
    server.writeCharacteristicValue({
      serviceId: serviceId,
      characteristicId: characteristicId,
      value: buffer,
      needNotify: true,
      success: () => {
        app.showModal({
          title: '',
          content: ''
        })
      },
      fail: (rej) => {
        console.log(rej)
        app.showToast({
          title: ''
        })
      }
    })
    
  }

  return (
    <PeripheralServerWrapper>
      <span className="title">periphral</span>
      <div className="content">
        <span className="info">{` serverId ${server.serverId}`}</span>
        <div className="item">
          <button className="button" onClick={createServer}>server</button>
          <button className="button" onClick={closeServer}>server</button>
        </div>
        <div className="item">
          <button className="button" onClick={addService}></button>
          <button className="button" onClick={removeService}></button>
        </div>
        <div className="item">
          <button className="button" onClick={startAdvertising}></button>
          <button className="button" onClick={stopAdvertising}></button>
        </div><div className="item">
          <button className="button" onClick={writeData}></button>
          <button className="button" onClick={closeBluetoothAdapter}></button>
        </div>
        <input type="number" pattern="\d*"  placeholder="" name="" value={valueToWrite} onChange={(e) => {
          setValueToWrite(e.target.value)
        }} /> 
      </div>
      
    </PeripheralServerWrapper>
  );
});

export default WPeripheralServer;