import React, { 
  memo, useCallback, useState
} from 'react';
import { Progress } from 'antd-mobile';


import  { DownloadWrapper } from "./style"
import app from "@/weapps"


const WDownloadFile = memo(() => {
  
  const [downloadTask, setDownloadTask] = useState(null)
  const [src, setSrc] = useState()
  const [isDownloading, setIsDownloading] = useState(false)
  const [progress, setProgress] = useState(0)

  const onHeadersReceived = useCallback((res) => {
    console.log(`header: ${res.header}`)
    console.log(`header-description: ${JSON.stringify(res.header)}`)
  }, [])

  const onProgressUpdate = useCallback((res) => {
    console.log(`progress: ${res.progress}`)
    setProgress(res.progress)
  }, [])

  return (
    <DownloadWrapper>
      <span className="title">download</span>
      <div className="content">
        {
          isDownloading && 
          <Progress style={{ width: "100%"}} percent={progress} position="normal" />
        }
        {
          src && <img src={src} alt="" onClick={(e) => {
            console.log(`src:${e.target.src}`)
            app.previewImage({
              urls: [src]
            })
          }} />
        }
        {
          !isDownloading && !src && <span></span>
        }

      </div>
      <button className="button" onClick={() => {
        if (downloadTask) {
          downloadTask.offHeadersReceived(onHeadersReceived)
          downloadTask.offProgressUpdate(onProgressUpdate)
        }
        setIsDownloading(true)
        setSrc(null)
        setProgress(0)
        const task = app.downloadFile({
          url: "https://www.tencent.com/img/banners/brief-1.jpg",
          success: (res) => {
            setIsDownloading(false)
            setSrc(res.tempFilePath)
          },
          fail: (res) =>{
            console.log("request fail:", res)
            setIsDownloading(false)
            setSrc(null)
          }
        })
        if (task) {
          task.onHeadersReceived(onHeadersReceived)
          task.onProgressUpdate(onProgressUpdate)
        }
        setDownloadTask(task)
      }}></button>
    </DownloadWrapper>
  );
});

export default WDownloadFile;