import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  "showNavigationBarLoading",
  "setNavigationBarTitle",
  "setNavigationBarColor",
  "hideNavigationBarLoading",
  "hideHomeButton"
]

const NavigationBar = {}

for (const api of apis) {
  NavigationBar[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default NavigationBar