import React, { 
  memo,
  useState
} from 'react';

import { Picker, List } from 'antd-mobile';

import  { PictureWapper } from "./style"

import app from "@/weapps"

const sizeType = [
  {
    label: '',
    value: ["camera"],
  },
  {
    label: '',
    value: ["album"],
  },
  {
    label: '',
    value: ["camera", "album"],
  },
];

const quality = [
  {
    label: '',
    value: ["original"],
  },
  {
    label: '',
    value: ["compressed"],
  },
  {
    label: '',
    value: ["original", "compressed"],
  },
]

const count = [
  {
    label: '1',
    value: 1,
  },
  {
    label: '2',
    value: 2,
  },
  {
    label: '3',
    value: 3,
  },
  {
    label: '4',
    value: 4,
  },
  {
    label: '5',
    value: 5,
  },
  {
    label: '6',
    value: 6,
  },
  {
    label: '7',
    value: 7,
  },
  {
    label: '8',
    value: 8,
  },
  {
    label: '9',
    value: 9,
  }
]


const WPicture = memo(() => {

  const [type, setType] = useState([sizeType[2].value])
  const [q, setQ] = useState([quality[2].value])
  const [c, setC] = useState([9])
  const [images, setImages] = useState([])

  return (
    <PictureWapper>
      <span className="title">choose/previewImage</span>
      {/* <div className="content"> */}
      <List style={{ backgroundColor: 'white' }} className="picker-list">
        <Picker value={type} data={sizeType} cols={1} onChange={v => {
          setType(v)
        }}>
          <List.Item arrow="horizontal"></List.Item>
        </Picker>
        <Picker value={q} data={quality} cols={1} onChange={v => {
          setQ(v)
        }}>
          <List.Item arrow="horizontal"></List.Item>
        </Picker>
        <Picker value={c} data={count} cols={1} onChange={v => {
          setC(v)
        }}>
          <List.Item arrow="horizontal"></List.Item>
        </Picker>
      </List>
      {/* </div> */}

      <div className="content">
        <span></span>
        <span className="count">{`${images.length}/${c}`}</span>
        <div className="img-ctn">
          
          { 
          images.map((image) => {
            return (
              <div className="img"  onClick={
                () => {
                  console.log("previewImage")
                  app.previewImage({
                    current: image,
                    urls: images,
                    success:() => {
                      console.log("previewImage success")
                    }
                  })
                }
              }>
                <img style={{ width:"100%", height: "100%" }} alt={image} src={`${image}`} key={image}></img>
              </div>
            )
          })
          }
          {
          images.length < c && <img className="img-btn" src={require("@/assets/image/plus_dark.png").default} onClick={() => {
            console.log("chooseImage")
            app.chooseImage({
              sourceType: type[0],
              sizeType: q[0],
              count: c[0],
              success: (res) => {
                setImages(res.tempFilePaths)
              }
            })
          }}></img>
          }
        </div>
      </div>
    </PictureWapper>
  );
});

export default WPicture