import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"

const apis = [
  "getSystemInfo"
]

const System = {}

for (const api of apis) {
  System[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default System
