import React, { 
  memo, 
  useState 
} from 'react';
import  { CompassWrapper } from "./style"

import app from "@/weapps"
import { useListenner } from "@/common/hook.js"


const WCompass = memo(() => {

  const [direction, setDirection] = useState(0)
  const [isMonitoring, setIsMonitoring] = useState(false)

  useListenner("onCompassChange", "offCompassChange", (res) => {
    setDirection(parseInt(res.direction, 10))
  })

  return (
    <CompassWrapper direction={direction}>
      <span className="title">onCompassChange</span>
      <span className="compass-title"></span>
      <div className="direction">
        <div className="bg-compass-line"></div>
        <img alt="" className="bg-compass" src={require("@/assets/image/compass.png").default}></img>
        <div className="direction-value">
          <span>{direction}</span>
          <span className="direction-degree">o</span>
        </div>
      </div>
      <div className="button-container">
        <button className="button" disabled={isMonitoring} className="button-item" onClick={
          () => {
            app.startCompass({
              success:() => {
                setIsMonitoring(true)
                console.log("startCompass success")
              },
              fail: () => {
                console.log("startCompass fail")
              }
            })
          }
        }></button>
        <button className="button" disabled={!isMonitoring} className="button-item" onClick={
          () => {
            app.stopCompass({
              success:() => {
                console.log("stopCompass success")
                setIsMonitoring(false)
              },
              fail: () => {
                console.log("stopCompass fail")
              }
            })
          }
        }></button>
      </div>
    </CompassWrapper>
  );
});

export default WCompass;