import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "stopBluetoothDevicesDiscovery",
  "startBluetoothDevicesDiscovery",
  "openBluetoothAdapter",
  "getConnectedBluetoothDevices",
  "getBluetoothDevices",
  "getBluetoothAdapterState",
  "closeBluetoothAdapter"
]

const onCallbacks = [
  "onBluetoothDeviceFound",
  "onBluetoothAdapterStateChange"
]

const offCallbacks = [
  "offBluetoothDeviceFound",
  "offBluetoothAdapterStateChange"
]

const Bluetooth = {}

for (const api of apis) {
  Bluetooth[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Bluetooth[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  Bluetooth[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default Bluetooth