import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  // "pageScrollTo"
]

const Scroll = {}

for (const api of apis) {
  Scroll[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default Scroll