import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "getNetworkType"
]

const onCallbacks = [
  "onNetworkStatusChange"
]

const offCallbacks = [
  "offNetworkStatusChange"
]

const Network = {}

for (const api of apis) {
  Network[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Network[callback] = (cb) => {
    onCallback(callback, cb)
  }
}

for (const callback of offCallbacks) {
  Network[callback] = (cb) => {
    offCallback(callback, cb)
  }
}
export default Network