import React, { 
  memo, 
  useState
} from 'react';
import  { GyroscopeWrapper } from "./style"
import app from "@/weapps"
import { useListenner } from "@/common/hook.js"


const WGyroscope = memo(() => {
  const [res, setRes] = useState(null)

  const [isMonitoring, setInMonitoring] = useState(false)

  useListenner("onGyroscopeChange", "offGyroscopeChange", (res) => {
    setRes(res)
  })

  return (
    <GyroscopeWrapper>
      <span className="title">onGyroscopeChange</span>
      <div className="content">
        <span className="gyroscope-title"></span>
        { res && res.x && 
          <div className="item">
            <span className="name">X</span>
            <span className="info">{res.x}</span>
          </div>
        }
        { res && res.y && 
          <div className="item">
            <span className="name">Y</span>
            <span className="info">{res.y}</span>
          </div>
        }
        { res && res.z && 
          <div className="item">
            <span className="name">Z</span>
            <span className="info">{res.z}</span>
          </div>
        }
      </div>
      <button className="button" disabled={isMonitoring} onClick={()=> {
        app.startGyroscope({
          success:() => {
            setInMonitoring(true)
            console.log("startGyroscope success")
          },
          fail:()=>{
            console.log("startGyroscope fail")
          }
        })
      }}></button>

      <button className="button" disabled={!isMonitoring} onClick={
        ()=> {
          app.stopGyroscope({
            success:() => {
              setInMonitoring(false)
              console.log("stopGyroscope success")
            },
            fail:()=>{
              console.log("stopGyroscope fail")
            }
          })
        }
      }></button>
    </GyroscopeWrapper>
  );
});

export default WGyroscope;