import React, { memo, useState } from 'react';
import { MonitorLocationWrapper } from "./style"
import app from "@/weapps"
import { useListenner } from "@/common/hook"

const WMonitorLocation = memo(() => {

  const [ startMonitoring, setStartMonitoring ] = useState(false)
  const [location, setLocation] = useState(null)

  useListenner("onLocationChange", "offLocationChange", (res) => {
    setLocation(res)
  })

  return (

    <MonitorLocationWrapper>
      <span className="title">monitorLocation</span>
      <div className="content">
        <span className="location-title"></span>
        { location && location.longitude && <div className="item">
          <span className="title"></span>
          <span className="info">{`${location.longitude.toFixed(7)}`}</span>
        </div> }
        { location && location.latitude && <div className="item">
          <span className="title"></span>
          <span className="info">{`${location.latitude.toFixed(7)}`}</span>
        </div> }
        { location && typeof location.speed == "number" && <div className="item">
          <span className="title"></span>
          <span className="info">{location.speed}</span>
        </div> }
        { location && location.accuracy && <div className="item">
          <span className="title"></span>
          <span className="info">{`${location.accuracy}`}</span>
        </div> }
        { location && location.altitude && <div className="item">
          <span className="title"></span>
          <span className="info">{`${location.altitude}`}</span>
        </div> }
        { location && location.verticalAccuracy && <div className="item">
          <span className="title"></span>
          <span className="info">{`${location.verticalAccuracy}`}</span>
        </div> }
        { location && location.horizontalAccuracy && <div className="item">
          <span className="title"></span>
          <span className="info">{`${location.horizontalAccuracy}`}</span>
        </div> }


      </div>

      <button className="button" onClick={
        () => {
          startMonitoring ? app.stopLocationUpdate({
            success:() => {
              console.log("stopLocationUpdate success")
              setStartMonitoring(false)
            },
            fail: () => {
              console.log("stopLocationUpdate fail")
            }
          }) : 
          app.startLocationUpdate({
            success:() => {
              console.log("startLocationUpdate success")
              setStartMonitoring(true)
            },
            fail: () => {
              console.log("startLocationUpdate fail")
            }
          })
        }
      }>{startMonitoring? "" : ""}</button>
    </MonitorLocationWrapper>
  );
});

export default WMonitorLocation;