import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "stopPullDownRefresh",
  "startPullDownRefresh"
]

const onCallbacks = [
  "onPullDownRefresh"
]

const offCallbacks = [
  "offPullDownRefresh"
]

const PullRefresh = {}


for (const api of apis) {
  PullRefresh[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  PullRefresh[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  PullRefresh[callback] = (cb) => {
    offCallback(callback, cb)
  }
}
export default PullRefresh