import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  "setBackgroundTextStyle",
  "setBackgroundColor"
]

const Background = {}

for (const api of apis) {
  Background[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default Background
