import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "stopDeviceMotionListening",
  "startDeviceMotionListening"
]

const onCallbacks = [
  "onDeviceMotionChange"
]

const offCallbacks = [
  "offDeviceMotionChange"
]

const DeviceMotion = {}

for (const api of apis) {
  DeviceMotion[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  DeviceMotion[callback] = (cb) => {
    onCallback(callback, cb)
  }
}

for (const callback of offCallbacks) {
  DeviceMotion[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default DeviceMotion