import React, { 
  memo,
  useState
} from 'react';

import  { ScanWapper } from "./style"

import app from "@/weapps"

const WScan = memo(() => {

  const [result, setResult] = useState("")

  return (
    <ScanWapper>
      <span className="title">scanCode</span>
      <span className="info"></span>
      <div className="content">
        <span className="result">{result}</span>
      </div>
      <button className="button" onClick={() => {
        app.scanCode({
          onlyFromCamera: true,
          success: (res) => {
            setResult(res.result)
          },
          fail: () => {
            console.log("scanCode success")
          }
        })
      }}></button>
      
    </ScanWapper>
  );
});

export default WScan