import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "stopAccelerometer",
  "startAccelerometer"
]

const onCallbacks = [
  "onAccelerometerChange"
]

const offCallbacks = [
  "offAccelerometerChange"
]


const Accelerometer = {}

for (const api of apis) {
  Accelerometer[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Accelerometer[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  Accelerometer[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default Accelerometer