import React, { memo } from 'react';
import app from "@/weapps"



const WButtonSheet = memo((props) => {
  const { buttonItems } = props
  return (
    <div style={{margin: "20px"}}>
      {
        buttonItems.map((buttonItem) => {
          return (
            <button className="button" key={buttonItem.api} onClick={
              () => {
                const params = typeof buttonItem.callback == "function" ? buttonItem.callback : {
                  ...buttonItem.data,
                  success: () => (console.log(`${buttonItem.title} success`)),
                  fail: () => (console.log(`${buttonItem.title} fail`)),
                  complete: () => (console.log(`${buttonItem.title} complete`)),
                }
                app[buttonItem.api](params)
              }
            }>{buttonItem.title}</button>
          )
        })
      }
    </div>
  );
});

export default WButtonSheet;