import React, { 
  memo,
  useEffect, 
  useState
} from 'react';

import  { NetworkWapper } from "./style"

import app from "@/weapps"
import { useListenner } from "@/common/hook.js"

const WNetwork = memo(() => {

  const [network, setNetwork] = useState(null)
  const [onNetwork, setOnNetwork] = useState(null)

  const className =  network ? "big-info" : "small-info";
  const onClassName = onNetwork ? "big-info" : "small-info";
  
  useListenner("onNetworkStatusChange", "offNetworkStatusChange", (res) => {
    if (res && res.isConnected) {
      setOnNetwork(res.networkType)
    } else {
      setOnNetwork("")
    }
  })

  useEffect(_ => {
    app.getNetworkType({
      success: (res) => {
        setOnNetwork(res.networkType)
      },
      fail: () => {
        console.log("getNetworkType fail")
      }
    })
  }, [])


  return (
    <NetworkWapper>
      <span className="title">getNetworkType</span>
      <div className="content">
        <span></span>
        <span className={className}>
          {network ? network : ""}
        </span>
      </div>
      <button className="button" onClick={() => {
        app.getNetworkType({
          success: (res) => {
            setNetwork(res.networkType)
          },
          fail: () => {
            console.log("getNetworkType fail")
          }
        })
      }}></button>
      <button className="button" onClick={_ => {
        setNetwork(null)
      }}></button>

      <span className="title">onNetworkStatusChange</span>
      <div className="content">
        <span></span>
        <span className={onClassName}>
          {onNetwork ? onNetwork : ""}
        </span>
      </div>
      
    </NetworkWapper>
  );
});

export default WNetwork;