import {
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"

const apis = [
  "stopLocationUpdate",
  "startLocationUpdateBackground",
  "startLocationUpdate",
  "openLocation",
  "getLocation",
  "chooseLocation"
]

const onCallbacks = [
  "onLocationChange"
]

const offCallbacks = [
  "offLocationChange"
]

const Location = {}

for (const api of apis) {
  Location[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Location[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  Location[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default Location