import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"

const apis = [
]

const onCallbacks = [
  "onMemoryWarning"
]

const offCallbacks = [
  "offMemoryWarning"
]

const Memory = {}


for (const api of apis) {
  Memory[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Memory[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  Memory[callback] = (cb) => {
    offCallback(callback, cb)
  }
}
export default Memory