import React, { memo, useCallback, useEffect, useState } from 'react';

import { BluetoothWrapper, CharacteristicWrapper } from "./style"
import { useListenner } from "@/common/hook"
import app from "@/weapps"

function inArray(arr, key, val) {
  for (let i = 0; i < arr.length; i++) {
    if (arr[i][key] === val) {
      return i
    }
  }
  return -1
}

// ArrayBuffer16
function ab2hex(buffer) {
  const hexArr = Array.prototype.map.call(
    new Uint8Array(buffer),
    function (bit) {
      return ('00' + bit.toString(16)).slice(-2)
    }
  )
  return hexArr.join('')
}

const WServiceItem = memo((props) => {

  const [characteristics, setCharacteristics] = useState([])

  useEffect(() => {
    app.getBLEDeviceCharacteristics({
      deviceId: props.deviceId,
      serviceId: props.serviceId,
      success: (res) => {
        setCharacteristics(res.characteristics)
      },
      fail: (res) => {
        console.log("getBLEDeviceCharacteristics fail: ", res)
      }
    })
  }, [props])

  return (
    <CharacteristicWrapper>
      <span className="title">{`UUID:${props.serviceId}`}</span>
      { characteristics.map((chara) => {
        console.log("prop:", chara)
        let properties = ""
        if (chara.properties.read) properties += "Read "
        if (chara.properties.write) properties += "Write "
        if (chara.properties.notify) properties += "Notify "
        if (chara.properties.indicate) properties += "Indicate"
        return (
          <div className="content" key={chara.uuid} onClick={()=>{
            props.click(props.deviceId, props.serviceId, chara)
          }}>
            <div >
              <span>{chara.uuid}</span>
              <span>{`Properties: ${properties}`}</span>
            </div>
        </div>
        )
      })}
    </CharacteristicWrapper>
  )
})

const WBluetooth = memo(() => {

  const [isScan, setIsScan] = useState(false)

  const [peripherals, setPeripherals] = useState([])

  const [canWrite, setCanWrrite] = useState(false)
  
  const [writeTo, setWriteTo] = useState(null)

  const [isConnect, setIsConnect] = useState(false)

  const [deviceId, setDeviceId] = useState(null)

  const [chs, setChs] = useState([])

  const [services, setServices] = useState([])

  const [name, setName] = useState("")

  

  const writeCharacteristicValue = useCallback((object) => {
    const buffer = new ArrayBuffer(1)
    const datadiv = new DataView(buffer)
    datadiv.setUint8(0, Math.random() * 19| 0)
    app.writeBLECharacteristicValue({
      ...object,
      value: buffer,
      success() {
        console.log('writeBLECharacteristicValue: ')
      },
      fail() {
        console.log('writeBLECharacteristicValue: ')
      },
      complete() {
        console.log('writeBLECharacteristicValue: ')

      }
    })
  }, [])

  const itemClickCallback = useCallback((deviceId, serviceId, characteristic) => {
    console.log("properties:", characteristic)
    if (characteristic.properties.read) {
      app.readBLECharacteristicValue({
        deviceId,
        serviceId,
        characteristicId: characteristic.uuid,
        success: (res)=> {
          // console.log("read:", JSON.stringify(res))
          console.log("readBLECharacteristicValue success")
        }
      })
    }
    if (characteristic.properties.write && characteristic.properties.notify) {
      setCanWrrite(true)
      setWriteTo({
        deviceId,
        serviceId,
        characteristicId: characteristic.uuid
      })
      writeCharacteristicValue({
        deviceId,
        serviceId,
        characteristicId: characteristic.uuid
      })
    }
    if (characteristic.properties.notify || characteristic.properties.indicate) {
      app.notifyBLECharacteristicValueChange({
        deviceId,
        serviceId,
        characteristicId: characteristic.uuid,
        state: true,
      })
    }
  }, [writeCharacteristicValue])

  const getCharacteristics = useCallback((deviceId, serviceId, characteristic) => {
    app.getBLEDeviceCharacteristics({
      deviceId,
      serviceId,
      success: (res) => {
        console.log('getBLEDeviceCharacteristics success', res.characteristics)
        for (let i = 0; i < res.characteristics.length; i++) {
          const item = res.characteristics[i]
          console.log("properties:", item.properties)
          if (item.properties.read) {
            app.readBLECharacteristicValue({
              deviceId,
              serviceId,
              characteristicId: item.uuid,
              success: (res)=> {
                // console.log("read:", JSON.stringify(res))
                console.log("readBLECharacteristicValue success")
              }
            })
          }
          if (item.properties.write && item.properties.notify) {
            setCanWrrite(true)
            setWriteTo({
              deviceId,
              serviceId,
              characteristicId: item.uuid
            })
            writeCharacteristicValue({
              deviceId,
              serviceId,
              characteristicId: item.uuid
            })
          }
          if (item.properties.notify || item.properties.indicate) {
            app.notifyBLECharacteristicValueChange({
              deviceId,
              serviceId,
              characteristicId: item.uuid,
              state: true,
            })
          }
        }
      }
    })
  }, [writeCharacteristicValue])


  const getService = useCallback((deviceId) => {
    app.getBLEDeviceServices({
      deviceId,
      success:(res) => {
        console.log('getBLEDeviceServices success', res.services)
        setServices(res.services)
        // setTimeout(() => {
        //   for (let i = res.services.length - 1; i >= 0; i--) {
        //     if (res.services[i].isPrimary) {
        //       getCharacteristics(deviceId, res.services[i].uuid)
        //       // return
        //     }
        //   }
        // }, 1);
      },
      fail: () => {
        console.log("getBLEDeviceServices fail")
      }
    })
  }, )

  

  
  

  useEffect(() => {
    app.openBluetoothAdapter({
      mode: "central",
      success:() => {
        console.log("openBluetoothAdapter success")
      },
      fail: (res) => {
        console.log("openBluetoothAdapter fail" + res)
      }
    })
    return () => {
      app.closeBluetoothAdapter({
        success:() => {
          console.log("closeBluetoothAdapter success")
        },
        fail: () => {
          console.log("closeBluetoothAdapter fail")
        }
      })
    }
  }, [])


  useListenner("onBluetoothDeviceFound", "offBluetoothDeviceFound", (res) => {
    setPeripherals(res.devices)
  })

  useListenner("onBLECharacteristicValueChange", "offBLECharacteristicValueChange", (characteristic) => {
    app.showToast({
      title: ''
    })

    const idx = inArray(chs, 'uuid', characteristic.characteristicId)
    const data = [...chs]
    const value = ab2hex(characteristic.value)
    if (idx === -1) {
      data.push({
        uuid: characteristic.characteristicId,
        value
      })
    } else {
      for (const item of data) {
        if (item.uuid === characteristic.characteristicId) {
          item.value = value
        }
      }
    }
    setChs(data)
  }, [chs])

  return (
    <BluetoothWrapper>
      <span className="func-title">bluetooth</span>
      <div className="content">
        <span>{`${peripherals.length}:`}</span>
        <div className="scroll">
          { peripherals.map((item) => {
            return (
              <div className="item" key={item.deviceId} onClick={()=> {
                app.showLoading()
                app.createBLEConnection({
                  deviceId: item.deviceId,
                  success: () => {
                    setIsConnect(true)
                    setDeviceId(item.deviceId)
                    setName(item.name)
                    getService(item.deviceId)
                    
                  },
                  fail: () => {
                    console.log("invoke createBLEConnection fail")
                  },
                  complete() {
                    app.hideLoading()
                  }
                })
              }}>
                <span className="title">{item.name}</span>
                <span>{`:${item.RSSI}`}</span>
                <span>{`UUID:${item.deviceId}`}</span>
                <span>{`Service:${(item.advertisServiceUUIDs && item.advertisServiceUUIDs.length) || 0
                }`}</span>
              </div>
            )
          }) }
        </div>
      </div>
      <button className="button" onClick={()=>{
        !isScan ?
        app.startBluetoothDevicesDiscovery({
          interval: 2,
          allowDuplicatesKey: true,
          success:()=>{
            setIsScan(true)
          },
          fail:()=>{
            console.log("startBluetoothDevicesDiscovery fail")
          }
        }) : 
        app.stopBluetoothDevicesDiscovery({
          success:() => {
            setIsScan(false)
          },
          fail:() => {
            console.log("stopBluetoothDevicesDiscovery fail")
          }
        })
      }}>{isScan ? "" : ""}</button>

      { isConnect && 
      <div className="connected_info">
        <div>
        <span>{name}</span>
          <div className="operation">
            { canWrite && <button className="button"  onClick={() => {
              writeCharacteristicValue(writeTo)
            }} className="min" ></button> }
            <button className="button" className="min" onClick={() => {
              app.closeBLEConnection({
                deviceId: deviceId,
                success: () => {
                  setIsConnect(false)
                  setCanWrrite(false)
                  setServices([])
                  setChs([])
                }
              })
            }} ></button>
          </div>
        </div>
        {chs.map((chara) => {
          return (
            <div key={chara.uuid}  style={{fontSize: "12px", marginTop: "10px"}}>
              <div>UUID: {chara.uuid}</div>
              <div>: {chara.value}</div>
            </div>
          )
        })}
      </div> }


      { 
      services.map((servive, index) => {
        return (
          <WServiceItem index={index} click={itemClickCallback} key={index} deviceId={deviceId} serviceId={servive.uuid} />
        )
      }) 
      }

      <div style={{height: "150px"}}></div>

    </BluetoothWrapper>
  );
});

export default WBluetooth;