import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  "checkIsOpenAccessibility"
]


const Accessibility = {}

for (const api of apis) {
  Accessibility[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default Accessibility