import {
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"

const apis = [
  "saveVideoToPhotosAlbum",
  "openVideoEditor",
  "getVideoInfo",
  "createVideoContext",
  "compressVideo",
  "chooseVideo",
  "chooseMedia"
]

const Video = {}

for (const api of apis) {
  Video[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}


export default Video