import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "stopGyroscope",
  "startGyroscope"
]

const onCallbacks = [
  "onGyroscopeChange"
]

const offCallbacks = [
  "offGyroscopeChange"
]
const Gyroscope = {}

for (const api of apis) {
  Gyroscope[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Gyroscope[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  Gyroscope[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default Gyroscope