import React, { 
  memo,
  useEffect, 
  useState
} from 'react';

import { Slider } from 'antd';
import  { ScreenWapper } from "./style"

import app from "@/weapps"
import { useListenner } from "@/common/hook.js"

const WScreen = memo(() => {

  const [brightness, setBrightness] = useState(0)

  const [info, setInfo] = useState("")
  
  useListenner("onUserCaptureScreen", "offUserCaptureScreen", (res) => {
    setInfo("")
  })

  useEffect(_ => {
    app.getScreenBrightness({
      success: (res) => {
        setBrightness(res.value.toFixed(1))
      },
      fail: () => {
        console.log("getScreenBrightness fail")
      }
    })
  }, [])


  return (
    <ScreenWapper>
      <span className="title">get/set/ScreenBrightness</span>
      <div className="content">
        <span></span>
        <span className="big-info">
          {brightness}
        </span>
      </div>
      <span className="info">  </span>
      <Slider className="slider" step={0.1} max={1.0} min={0} value={brightness} onChange={(value) => {
        app.setScreenBrightness({
          value,
          success:() => {
            console.log("setScreenBrightness success")
            setBrightness(value)
          },
          fail: () => {
            console.log("setScreenBrightness fail")
          }
        })
      }} />

      <span className="title">onUserCaptureScreen</span>
      <div className="content">
        <span></span>
        <span className={"big-info-light"}>
          {info}
        </span>
      </div>
    </ScreenWapper>
  );
});

export default WScreen