import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"

import Foundation from "../foundation"
import {
  utf8StringToArrayBuffer,
  arrayBufferToUtf8String
} from "../utils"

class RequestTask {
  constructor(identifier,type) {
    this.identifier = identifier
    this.type = type
  }
}

RequestTask.prototype.abort = function(object) {
  if (typeof object !== "object") {
    object = {}
  }
  const callbackName = createOnceCallback(object)
  object.identifier = this.identifier
  object.type = this.type
  callNativeApi("abort", washObject(object), callbackName)
}

const subOnCallbacks = [
  "onHeadersReceived",
  "onProgressUpdate"
]

const subOffCallbacks = [
  "offHeadersReceived",
  "offProgressUpdate"
]

for (const callback of subOnCallbacks) {
  RequestTask.prototype[callback] = function(cb) {
    const data = {
      identifier: this.identifier,
      type: this.type
    }
    onCallback(callback, cb, data, callback)
  }
}



for (const callback of subOffCallbacks) {
  RequestTask.prototype[callback] = function(cb) {
    const data = {
      identifier: this.identifier,
      type: this.type
    }
    offCallback(callback, cb, data)
  }
}


//######################################


const Request = {}


Request.request = (object = {}) => {
  if (object.data instanceof ArrayBuffer) {
    object.data = arrayBufferToUtf8String(object.data)
  }
  if (typeof object.success === "function") {
    object.success.wrapper = (res) => {
      //string
      const data = res.data
      if (object.responseType === "arraybuffer") {
        try {
          data = utf8StringToArrayBuffer(data)
          res.data = data
        } catch (e) {
          console.log("wx.request utf8StringToArrayBuffer fail")
        }
      }
      return res
    }
  }
  const callbackName = createOnceCallback(object)
  const taskId = callNativeApi("request", washObject(object), callbackName)
  if (taskId === "null") return null
  return new RequestTask(taskId, "requestTask")
}


Request.downloadFile = (object = {}) => {
  const callbackName = createOnceCallback(object)
  const taskId = callNativeApi("downloadFile", washObject(object), callbackName)
  if (taskId === "null") return null
  return new RequestTask(taskId, "downloadTask")
}

Request.uploadFile = (object = {}) => {
  const callbackName = createOnceCallback(object)
  const taskId = callNativeApi("uploadFile", washObject(object), callbackName)
  if (taskId === "null") return null
  return new RequestTask(taskId, "uploadTask")
}

export default Request
