import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "hideKeyboard"
]

const onCallbacks = [
  "onKeyboardHeightChange"
]

const offCallbacks = [
  "offKeyboardHeightChange"
]

const Keyboard = {}

for (const api of apis) {
  Keyboard[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Keyboard[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  Keyboard[callback] = (cb) => {
    offCallback(callback, cb)
  }
}
export default Keyboard