import React, { memo, useState } from 'react';
import  {GetLocationWrapper} from "./style"
import app from "@/weapps"

const WGetLocation = memo(() => {
  const [location, setLocation] = useState(null)

  return (
    <GetLocationWrapper>
      <span className="title">getLocation</span>
      <div className="content">
        <span className="location-title"></span>
        { location && <span className="location">{location}</span> }
      </div>
      <button className="button" onClick={()=> {
        app.getLocation({
          type: "wgs84",
          altitude: true,
          isHighAccuracy: false,
          success: (res) => {
            setLocation(`E:${res.longitude.toFixed(2)} N:${res.latitude.toFixed(2)}`)
          },
          fail:(res) => {
            console.log(res)
          },
          complete:() => {
            console.log("getLocation complete")
          }
        })
      }}></button>

      <button className="button" onClick={
        ()=> setLocation(null)
      }></button>
    </GetLocationWrapper>
  );
});

export default WGetLocation;