function *IdGenerator() {
  let id = 0
  while(true) {
    yield ++id
  }
}


const preCallbackName = "callbackName__"
const idGenerator = IdGenerator()

const _createCallback = ({success, fail, complete}, isOnce = true) => {
  let callbackName = "null"
  if (typeof success === "function" || typeof fail === "function" || typeof complete === "function") {
    callbackName = preCallbackName + idGenerator.next().value
    window[callbackName] = (err, res) => {
      if (err === "null" && typeof success === "function") {
        try {
          let object = JSON.parse(res)
          //success
          if (typeof success.wrapper === "function") {
            object = success.wrapper(object)
          }
          success(object)
        } catch(error){
          console.log(error)
        }
      }
      if (err !== "null" && typeof fail === "function") {
        try {
          fail(JSON.parse(err))
          console.log(callbackName, err)
        } catch (error) {
          console.log(error)
        }
      }
      if (typeof complete === "function") {
        complete()
      }
      if (isOnce) {
        delete window[callbackName]
      }
    }
  }
  return callbackName
}

export const onCallback = (apiName, callback, data = {}, callbackname = apiName) => {
  let cbName = callbackname + idGenerator.next().value
  window[cbName] = (err, res) => {
    if (err === "null" && typeof callback === "function") {
      try {
        let object = JSON.parse(res)
        if (typeof callback.wrapper === "function") {
          object = callback.wrapper(object)
        }
        callback(object)
      } catch(error){
        console.log(error)
      }
    }
  }
  window[cbName].innerCallback = callback
  return callNativeApi(apiName, data, cbName)
}

export const offCallback = (apiName, callback, data ={}) => {
  for (const key in window) {
    if (window.hasOwnProperty(key)) {
      const cb = window[key];
      if (cb && cb.innerCallback === callback) {
        callNativeApi(apiName, data, key)        
        //callback
        delete window[key]
        return
      }
    }
  }
}

//
export const createOnceCallback = (object) => {
  if (typeof object !== "object") {
    object = {}
  }
  return _createCallback(object)
}

//
export const createCallback = (object) => {
  if (typeof object !== "object") {
    object = {}
  }
  return _createCallback(object, false)
}

export const deleteCallback = (callbackName) => {
  delete window[callbackName]
}

export const callNativeApi = (apiName, data, callbackName) => {
  //
  if (typeof data.wrapper === "function") {
    data = data.wrapper(data)
  }
  const params = {
    callback: callbackName,
    data: data
  }
  try {
    return window.__weappsNative[apiName](JSON.stringify(params))
  }catch (err) {
    console.log(`fail to call native api: ${apiName}`)
    console.log(err.toString())
  }
}


export const washObject = (object) => {
  let obj = {...object}
  delete obj.success
  delete obj.fail
  delete obj.complete
  return obj
}