import React, { memo, useState } from 'react';
import { OpenLocationWrapper } from "./style"
import app from "@/weapps"

const WOpenLocation = memo(() => {
  const [ location ] = useState({
    latitude:23.099994,
    longitude:113.324520,
    name:"T.I.T",
    address:"397"
  })

  return (
    <OpenLocationWrapper>
      <span className="title">openLocation</span>
      <div className="content">
        <div className="item">
          <span className="name"></span>
          <span className="info">{location.longitude}</span>
        </div>
        <div className="item">
          <span className="name"></span>
          <span className="info">{location.latitude}</span>
        </div>
        <div className="item">
          <span className="name"></span>
          <span className="info">{location.name}</span>
        </div>
        <div className="item">
          <span className="name"></span>
          <span className="info">{location.address}</span>
        </div>
      </div>
      <button className="button" onClick={()=> {
        app.openLocation({
          ...location,
          success: () => {
            console.log("openLocation success")
          },
          fail:() => {
            console.log("openLocation fail")
          },
          complete: () => {
            console.log("openLocation complete")
          }
        })
      }}></button>
    </OpenLocationWrapper>
  );
});

export default WOpenLocation;