import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "stopWifi",
  "startWifi",
  "startWifi",
  "getWifiList",
  "getConnectedWifi",
  "connectWifi"
]

const onCallbacks = [
  "onWifiConnected",
  "onGetWifiList"
]

const offCallbacks = [
  "offWifiConnected",
  "offGetWifiList"
]

const Wifi = {}

for (const api of apis) {
  Wifi[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Wifi[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  Wifi[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default Wifi