
import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "stopBeaconDiscovery",
  "startBeaconDiscovery",
  "getBeacons"
]

const onCallbacks = [
  "onBeaconUpdate",
  "onBeaconServiceChange"
]

const offCallbacks = [
  "offBeaconUpdate",
  "offBeaconServiceChange"
]

const IBeacon = {}

for (const api of apis) {
  IBeacon[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  IBeacon[callback] = (cb) => {
    onCallback(callback, cb)
  }
}


for (const callback of offCallbacks) {
  IBeacon[callback] = (cb) => {
    offCallback(callback, cb)
  }
}

export default IBeacon