import React, { 
  memo,
  useState
} from 'react';

import { Picker, List } from 'antd-mobile';

import  { VideoWapper } from "./style"

import app from "@/weapps"

const sizeType = [
  {
    label: '',
    value: ["camera"],
  },
  {
    label: '',
    value: ["album"],
  },
  {
    label: '',
    value: ["camera", "album"],
  },
];

const camera = [
  {
    label: '',
    value: "front",
  },
  {
    label: '',
    value: "back",
  },
]

let maxDuration = []

for (let i = 1; i<= 60; i++) {
  maxDuration.push({
    label: i + "",
    value: i
  })
}


const WVideo = memo(() => {

  const [type, setType] = useState([sizeType[2].value])
  const [q, setQ] = useState(["back"])
  const [c, setC] = useState([60])
  const [src, setSrc] = useState(null)

  return (
    <VideoWapper>
      <span className="title">choose/previewImage</span>
      <List style={{ backgroundColor: 'white' }} className="picker-list">
        <Picker value={type} data={sizeType} cols={1} onChange={v => {
          setType(v)
        }}>
          <List.Item arrow="horizontal"></List.Item>
        </Picker>
        <Picker value={q} data={camera} cols={1} onChange={v => {
          setQ(v)
        }}>
          <List.Item arrow="horizontal"></List.Item>
        </Picker>
        <Picker value={c} data={maxDuration} cols={1} onChange={v => {
          setC(v)
        }}>
          <List.Item arrow="horizontal"></List.Item>
        </Picker>
      </List>

      <div className="content">
        { src && 
        <video src={src}  style={{width: "300px", height: "300px"}} className="video" x-webkit-airplay="true" playsinline="true" webkit-playsinline="true" controls="controls"></video>
        }
        { !src && 
        <div className="add" onClick={() => {
          app.chooseVideo({
            sourceType: type[0],
            maxDuration: c[0],
            camera: q[0],
            success: (res) => {
              setSrc(res.tempFilePath)
            }
          })
        }}>
          <img src={require("@/assets/image/plus.png").default} alt="" className="img"/>
          <span className="title"></span>
        </div>
        }
      </div>
    </VideoWapper>
  );
});

export default WVideo