import { 
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"


const apis = [
  "addPhoneContact"
]

const Contact = {}

for (const api of apis) {
  Contact[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

export default Contact