import React, { 
  memo, 
  useState
} from 'react';
import  { DevieMotionWrapper } from "./style"
import app from "@/weapps"
import { useListenner } from "@/common/hook.js"


const WDevieMotion = memo(() => {
  const [res, setRes] = useState(null)

  const [isMonitoring, setInMonitoring] = useState(false)

  useListenner("onDeviceMotionChange", "offDeviceMotionChange", (res) => {
    setRes(res)
  })

  return (
    <DevieMotionWrapper>
      <span className="title">onDeviceMotionChange</span>
      <div className="content">
        <span className="motion-title"></span>
        { res && res.alpha && 
          <div className="item">
            <span className="name">alpha</span>
            <span className="info">{res.alpha}</span>
          </div>
        }
        { res && res.beta && 
          <div className="item">
            <span className="name">beta</span>
            <span className="info">{res.beta}</span>
          </div>
        }
        { res && res.gamma && 
          <div className="item">
            <span className="name">gamma</span>
            <span className="info">{res.gamma}</span>
          </div>
        }
      </div>
      <button className="button" disabled={isMonitoring} onClick={()=> {
        app.startDeviceMotionListening({
          success:() => {
            setInMonitoring(true)
            console.log("startDeviceMotionListening success")
          },
          fail:()=>{
            console.log("startDeviceMotionListening fail")
          }
        })
      }}></button>

      <button className="button" disabled={!isMonitoring} onClick={
        ()=> {
          app.stopDeviceMotionListening({
            success:() => {
              setInMonitoring(false)
              console.log("stopDeviceMotionListening success")
            },
            fail:()=>{
              console.log("stopDeviceMotionListening fail")
            }
          })
        }
      }></button>
    </DevieMotionWrapper>
  );
});

export default WDevieMotion;