import { 
  callNativeApi, 
  createOnceCallback, 
  onCallback,
  offCallback,
  washObject 
} from "../base"


const apis = [
  "setScreenBrightness",
  "setKeepScreenOn",
  "getScreenBrightness"
]

const onCallbacks = [
  "onUserCaptureScreen"
]

const offCallbacks = [
  "offUserCaptureScreen"
]

const Screen = {}

for (const api of apis) {
  Screen[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}

for (const callback of onCallbacks) {
  Screen[callback] = (cb) => {
    onCallback(callback, cb)
  }
}

for (const callback of offCallbacks) {
  Screen[callback] = (cb) => {
    offCallback(callback, cb)
  }
}
export default Screen