import {
  callNativeApi, 
  createOnceCallback, 
  washObject 
} from "../base"

const apis = [
  "saveImageToPhotosAlbum",
  "previewMedia",
  "previewImage",
  "getImageInfo",
  "compressImage",
  "chooseMessageFile",
  "chooseImage",
  "getBase64Image",
  "saveBase64Image"
]

const Picture = {}

for (const api of apis) {
  Picture[api] = (object) => {
    const callbackName = createOnceCallback(object)
    callNativeApi(api, washObject(object), callbackName)
  }
}


export default Picture